
<template>
  <div>

      <div>
        <div class="table-wrap-style">
          <Table    :data="tableData" :columns="tableColumns"  stripe border></Table>
        </div>
      </div>


  </div>
</template>
<script>

  export default {
    name:'expendTable',
    data(){

      return{
       /* nextDetailWrap:false,*/
 /*       tableData:[
          {hasnext:1,icon:'ios_icon',child_id_app:12,user_no:'用户管理',create_time:'2018-12-12 12:12:12',update_time:'2018-12-12 12:12:12',operate_user:'/user',child_desc_app:'12'},
          {hasnext:0,icon:'ios_icon',child_id_app:12,user_no:'统计查询',create_time:'2018-12-12 12:12:12',update_time:'2018-12-12 12:12:12',operate_user:'/tongji',child_desc_app:'11'}
        ],*/
        /*table表格*/
        tableColumns: [
          {
            title: '菜单id',
            align: 'center',
            width:150,
            /*  sortable: 'custom',*/
            key:'child_desc_app'
          },
          {
            title: '菜单名称',
            key: 'user_no',
            align: 'center',
            /* sortable: 'custom',*/
            width:150
          },
          {
            title: '菜单url',
            key: 'operate_user',
            align: 'center',
          },
          {
            title: '菜单级别',
            align: 'center',
            key: 'create_time',
          },
          {
            title: '上级菜单',
            align: 'center',
            key: 'update_time',
          },
          {
            title: '排序号',
            align: 'center',
            key: 'update_time',
          },
          {
            title: '操作',
            key: 'action',
            width: 300,
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      console.log(params.index)
                      this.addWrap(params);
                    }
                  }
                }, '添加子级'),
                h('Button', {
                  props: {
                    type: 'error',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      console.log(params.index)
                      this.deleteInfo(params)
                    }
                  }
                }, '删除'),
                h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      console.log(params.index)
                      this.editInfo()
                    }
                  }
                }, '编辑'),
                h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    display:params.row.hasnext==1?'':'none'
                  },
                  on: {
                    click: () => {
                      console.log(params.index)
                      this.showNextWrap(params)
                    }
                  }
                }, '展开子菜单')
              ]);
            }
          }

        ]
      }
    },
    props: {
      tableData: Object,
      nextDetailWrap:Boolean,
    },
    methods: {
      addWrap(data){
        this.$emit("addWrap",data)
      },
      deleteInfo(data){
        this.$emit("deleteInfo",data)
      },
      editInfo(data){
        this.$emit("editInfo",data)
  },
      showNextWrap(data){
        console.log(111)
        this.$emit("showNextInfo",data)
      },

    }
  };
</script>
<style scoped>
  .expand-row{
    margin-bottom: 16px;
  }
</style>



