export const tableMethods = {
  data(){
    return{
      pageSizeOpts:[10, 30, 50, 100],
      tableData: [],
      total: 0,
      page:1,
      size:10,
      tableHeight:200,
      loading:true,
      showMore:false,
    }
  },
  mounted() {
    //重新计算table的高度
    this.resizeTabeleHeight()
  },
  watch:{
    showMore(){
      this.resizeTabeleHeight()
    },
  },
  methods: {
    resizeTabeleHeight(){
      this.$nextTick(function () {
        //重新计算table的高度
        this.tableHeight=this.$refs.tableBox.clientHeight;
      })
    },
    //点击查询需要初始化页码，
    reSearchInfo(){
      this.page=1;
      this.searchInfo();
    },
    //修改页码 ，重新加载数据
    changePage (row) {
      this.page = row;
      this.searchInfo();
    },
    //修改每页展示的数据条数
    changeSize(size){
      this.size=size
      this.page=1
      this.searchInfo();
    },
    //导出数据
    exportInfo(){
      console.log(111)
      this.$Modal.confirm({
        title: '提示',
        content: `确定导出吗？`,
        onOk: ()=>{
          this.exportOk()
        }
      })
    },
  }

}



